// 樣式-小比例尺
function courtStyle(feature) {
    var emptyStyle = new Style({});

    var styleAll = new Style({
        image: new CircleStyle({
            radius: 5,
            fill: new Fill({
                color: [100, 100, 200, 1]
            })
        }),
        fill: new Fill({
            color: 'rgba(255, 255, 0, 0.6)'
        }),
        stroke: new Stroke({
            color: '#319FD3',
            width: 1
        }),
        text: new Text({
            font: '12px Microsoft JhengHei',
            fill: new Fill({
                color: '#BF5381'
            }),
            stroke: new Stroke({
                color: '#FFFFFF',
                width: 3
            }),
            offsetX: 40,
            offsetY: -20
        })
    });

    var styleClay = new Style({
        image: new CircleStyle({
            radius: 5,
            fill: new Fill({
                color: [198, 74, 21, 1]
            })
        }),
        fill: new Fill({
            color: 'rgba(255, 255, 0, 0.6)'
        }),
        stroke: new Stroke({
            color: '#319FD3',
            width: 1
        }),
        text: new Text({
            font: '12px Microsoft JhengHei',
            fill: new Fill({
                color: '#BF5381'
            }),
            stroke: new Stroke({
                color: '#FFFFFF',
                width: 3
            }),
            offsetX: 40,
            offsetY: -20
        })
    });

    var styleHard = new Style({
        image: new CircleStyle({
            radius: 5,
            fill: new Fill({
                color: [33, 93, 235, 1]
            })
        }),
        fill: new Fill({
            color: 'rgba(10, 10, 255, 0.6)'
        }),
        stroke: new Stroke({
            color: '#319FD3',
            width: 1
        }),
        text: new Text({
            font: '12px Microsoft JhengHei',
            fill: new Fill({
                color: '#BF5381'
            }),
            stroke: new Stroke({
                color: '#FFFFFF',
                width: 3
            }),
            offsetX: 40,
            offsetY: -10
        })
    });

    // 依條件賦予樣式
    let styleCurrentSurface;
    if (courtSelection.surface.includes(feature.get('surface')) && courtSelection.courtType.includes(feature.get('type'))) {
        if (feature.get('surface') === '紅土[clay]') {
            styleCurrentSurface = styleClay;
        }
        else if (feature.get('surface') === '硬地[hard]') {
            styleCurrentSurface = styleHard;
        }
        else {
            styleCurrentSurface = styleAll;
        }
        return [styleCurrentSurface];
    }
    else {
        return [emptyStyle];
    }
}


