// 樣式-小比例尺夜光地圖
function courtStyleTheme(feature) {
    var emptyStyle = new Style({});

    var styleNoLight = new Style({
        image: new CircleStyle({
            radius: 5,
            fill: new Fill({
                color: [100, 100, 200, 1]
            })
        }),
        fill: new Fill({
            color: 'rgba(45, 10, 115, 0.6)'
        }),
        stroke: new Stroke({
            color: '#319FD3',
            width: 1
        }),
        text: new Text({
            font: '12px Microsoft JhengHei',
            fill: new Fill({
                color: '#BF5381'
            }),
            stroke: new Stroke({
                color: '#FFFFFF',
                width: 3
            }),
            offsetX: 40,
            offsetY: -20
        })
    });

    var styleLight = new Style({
        image: new CircleStyle({
            radius: 5,
            fill: new Fill({
                color: [243, 255, 88, 1]
            })
        }),
        fill: new Fill({
            color: 'rgba(243, 255, 88, 0.6)'
        }),
        stroke: new Stroke({
            color: '#319FD3',
            width: 1
        }),
        text: new Text({
            font: '12px Microsoft JhengHei',
            fill: new Fill({
                color: '#BF5381'
            }),
            stroke: new Stroke({
                color: '#FFFFFF',
                width: 3
            }),
            offsetX: 40,
            offsetY: -20
        })
    });

    // 依條件賦予樣式
    let styleCurrentSurface;
    if (feature.get('light') === true) {
        styleCurrentSurface = styleLight;
    }
    else if (feature.get('light') === false) {
        styleCurrentSurface = styleNoLight;
    }
    else {
        styleCurrentSurface = emptyStyle;
    }
    return [styleCurrentSurface];

}







