// 場地篩選條件物件
let courtSelection = {
    surface: ['紅土[clay]', '硬地[hard]', '草地[grass]'],
    courtType: ['其他[other]', '', '私人場地[private]', '高中或國中小[School]', '公共設施[Public]', '公共設施[Public]', '大專院校[University]'],
};

// 表面材質
$("input.checkboxSurface").click(function () {
    courtSelection.surface = [];
    $("input.checkboxSurface:checked").each(function () {
        let value = $(this).val();
        courtSelection.surface.push(value);
    });
    v_tennis_court.setStyle(courtStyle);
    v_tennis_court_large_scale.setStyle(courtStyleLargeScale);


})

// 場地所有者
$("input.checkboxOwnerType").click(function () {
    courtSelection.courtType = [];
    $("input.checkboxOwnerType:checked").each(function () {
        let value = $(this).val();
        courtSelection.courtType.push(value);
    });
    v_tennis_court.setStyle(courtStyle);
    v_tennis_court_large_scale.setStyle(courtStyleLargeScale);

})


// 地圖日夜切換
$("#themeLayer").click(function () {
    if ($("#themeLayer").prop("checked")) {
        baseLayerGroup.getLayers().forEach(function (element, index, array) {
            let baseLayerTitle = element.get('title');
            element.setVisible(baseLayerTitle === 'cartoDark');
        })
        v_tennis_court.setStyle(courtStyleTheme);

    }
    else {
        baseLayerGroup.getLayers().forEach(function (element, index, array) {
            let baseLayerTitle = element.get('title');
            element.setVisible(baseLayerTitle === 'cartoLight');
        })
        v_tennis_court.setStyle(courtStyle);

    }
});




